export default {
	props: {
		scoreNum: {
			type: Number,
			default() {
				return 4.3
			}
		}
	},
	data() {
		return {
			// 评分比例
			scoreProportion: 0
		}
	},
	created() {
		this.calculation(this.scoreNum)
	},
	methods:{
		// 评分比例计算
		calculation(score) {
			
			let maxScore = 5
			this.scoreProportion = (score/maxScore*100) >= 100? 100: (score/maxScore*100)
			
			
		}
	}
}	
